import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useParams } from 'react-router-dom';
import { fetchData } from './actions';
import { Container, DetailTitle, DetailBody } from './styles';

const Detail = () => {
  const { id } = useParams();
  const dispatch = useDispatch();
  const { data, loading, error } = useSelector(state => state);

  useEffect(() => {
    dispatch(fetchData());
  }, [dispatch]);

  if (loading) {
    return <div>Loading...</div>;
  }

  if (error) {
    return <div>Error: {error}</div>;
  }

  const selectedItem = data.find(item => item.id === parseInt(id));

  if (!selectedItem) {
    return <div>Item not found.</div>;
  }

  return (
    <Container>
      <h1>Detail Page for Post with ID</h1>
      <DetailTitle>User ID:</DetailTitle>
      <DetailBody>Title: Full Title here</DetailBody>
      <DetailBody>Body: Full Body here</DetailBody>
    </Container>
  );
};

export default Detail;
